import React, { Component } from 'react';
import MaterialTable from 'material-table';
import Link from '@material-ui/core/Link';
import {db} from '../firebase';

export default class TableJunaeb extends Component {


    state = {
        data: []
    }

    componentDidMount(){
  
        const newData = []
        const collectionDB = db.collection('dondecomprarjunaeb');
        const every_document = collectionDB.get().then(snapshot => {
            snapshot.forEach(doc => {
                newData.push(doc.data());
            });
            this.setState({data: newData});
        });
    }
      

    
    render() {
        return (
            <div className="table">
                <MaterialTable
          columns={[
            { title: 'Nombre', field: 'name' },
            { title: 'Producto', field: 'product' },
            { title: 'Página', field: 'page', render: rowData =>  <Link href={rowData.page} color="inherit" color="primary">
            {rowData.page}
          </Link>  },
            {title: 'Dirección', field: 'direction'}
     
          ]}
          data={this.state.data}
          title="Productos a comprar con junaeb"
        />
            </div>
        )
    }
}
