import React, { Component } from 'react'
import {db} from '../firebase';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import SnackBarSucces from './SnackBarSuccess';
import SnackBarFail from './SnackBarFail';

export default class TextContent extends Component {
    constructor(props){
        super(props);
        this.inputName = React.createRef();
        this.inputProduct = React.createRef();
        this.inputPage = React.createRef();
        this.inputDirection = React.createRef();
    }

    state = {
        open: false,

        success: false,
        error: false
    }



    handleClickOpen = () => {
        this.setState({open: !this.state.open})
      };
    
    handleSendInfo = () => {

        //Validation

      
        let expressionOthers = /^[a-zA-Z\d,\s]{2,}$/gm;
        let regexOther = new RegExp(expressionOthers);

    
        if(!this.inputName.value.match(regexOther) || !this.inputProduct.value.match(regexOther)   ){
            this.handleClickOpen();
            this.setState({ error: true});
            console.log("ERROR OTHERS");
            return;
        }



        const newItem = {
            name: this.inputName.value,
            product: this.inputProduct.value,
            page: this.inputPage.value,
            direction: this.inputDirection.value
        }
        db.collection('dondecomprarjunaeb').doc().set(newItem).then(created => {
            this.handleClickOpen();
            this.setState({succes: true});
        }).catch(e => {
            this.handleClickOpen();
            this.setState({ error: true});
        });
    }
    


    render() {
        const SnackBar = this.state.success === true ? <SnackBarSucces></SnackBarSucces>: <SnackBarFail></SnackBarFail>;
        
        return (
           <div className="textContent">
               <h1>¿Donde comprar con la tarjeta junaeb?</h1>
               <p>Proyecto creado con la función de ordenar todos los locales/páginas en donde se puede utilizar esta tarjeta.</p>
               <div className="buttonsContainer"> 
                <Button color="primary" variant="contained" onClick={this.handleClickOpen}>Conozco un local</Button>
               <Button color="secondary" variant="contained" href="https://www.sodexo.cl/becajunaeb/">Acerca Junaeb Sodexo</Button>
               </div>

               <Dialog open={this.state.open} onClose={this.handleClickOpen} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Agregar un Producto </DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                        Esta página esta creada para poder ayudar a la comunidad a encontrar productos con mayor facilidad, se agredece cada aporte :)
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Nombre de local/página"
                        autoComplete="off"
                        inputRef={value => this.inputName = value}
                        fullWidth
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Breve descripción de producto"
                        autoComplete="off"
                        inputRef={value => this.inputProduct = value}
                        fullWidth
                    />
                    <p className="indications">*Pueden ser tags como : sushi, proteinas, vegan, completos, etc.</p>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Página web"
                        type="url"
                        autoComplete="off"
                        inputRef={value => this.inputPage = value}
                        fullWidth
                    />
                <p className="indications">*Opcional formato: https://pagina.cl</p>

                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Dirección"
                        autoComplete="off"
                        inputRef={value => this.inputDirection = value}
                        fullWidth
                    />
                                    <p className="indications">*Opcional</p>

                    </DialogContent>
                    <DialogActions>
                    <Button onClick={this.handleClickOpen} color="primary">
                        Cancelar
                    </Button>
                    <Button onClick={this.handleSendInfo} color="primary">
                        Agregar
                    </Button>
         
                    </DialogActions>
      </Dialog>
    {this.state.succes === true ? <SnackBarSucces></SnackBarSucces> : null}
    {this.state.error === true ? <SnackBarFail></SnackBarFail> : null}


              
           </div>
        )
    }
}
